<template>
<div class="file-browser">
  <table>
    <thead>
      <tr>
        <th colspan="2" class="path">
          <path-breadcrumb :path="path" @change="handleBreadClick"></path-breadcrumb>
        </th>
      </tr>
      <tr>
        <th>Name</th>
        <th>Size</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="file in files"
          :key="file.name"
          @click="handleClick(file)">
        <td>{{file.name}}<span v-if="file.dir">/</span></td>
        <td><span v-if="!file.dir">{{file.size}}</span></td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
  import PathBreadcrumb from './PathBreadcrumb'

export default {
  components: {
    PathBreadcrumb
  },
  data: function() {
    return {
      path: [],
      files: []
    }
  },
  methods: {
    fetchList: function() {
      let params = { path: '/' + this.path.join('/') }
      this.$http
        .get('http://localhost:8080/list', { params })
        .then(response => {
          this.files = response.body
        })
    },
    handleClick: function(file) {
      if (file.dir) {
        this.path.push(file.name)
        this.visitPath();
      }
    },
    handleBreadClick: function(path) {
      this.path = path;
      this.visitPath();
    },
    visitPath: function() {
      history.pushState({ path: this.path }, '', '/' + this.path.join('/'))
      this.fetchList();
    }
  },
  created: function() {
    this.path = location.pathname.split('/').filter(String)

    this.fetchList();
    history.replaceState({ path: this.path }, location.pathname)

    window.onpopstate = (event) => {
      console.log(event);
      this.path = event.state.path;
      this.fetchList();
    }
  }
}
</script>

<style lang="sass">
  table
    text-align: left
    width: 100%
    max-width: 50em
    border: 1px solid #ddd
    margin: 0 auto
    border-collapse: collapse

td, th
  padding: 0.5em
  border: 0
  margin: 0

tbody tr:hover
  background: #f5f5f5
  cursor: pointer

.path
  border-bottom: 1px solid #ddd

</style>
